"use client";

import React, { useState } from "react";

interface UploadToR2Props {
  onUploaded?: (publicUrl: string, key: string, filename?: string) => void;
  accept?: string;
}

export function UploadToR2({ onUploaded, accept = "*/*" }: UploadToR2Props) {
  const [uploading, setUploading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleFile = async (file: File) => {
    setUploading(true);
    setError(null);

    try {
      // 1) Ask server for presigned URL
      const presignRes = await fetch("/api/r2/presign", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ filename: file.name, contentType: file.type || "application/octet-stream" }),
      });

      if (!presignRes.ok) throw new Error("Gagal meminta URL upload");

      const { url, key, publicUrl } = await presignRes.json();

      // 2) Upload directly to R2 using PUT
      const putRes = await fetch(url, {
        method: "PUT",
        headers: { "Content-Type": file.type || "application/octet-stream" },
        body: file,
      });

      if (!putRes.ok) throw new Error("Upload ke R2 gagal");

      onUploaded?.(publicUrl, key, file.name);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Terjadi kesalahan");
    } finally {
      setUploading(false);
    }
  };

  return (
    <div>
      {error && <p className="text-sm text-destructive">{error}</p>}
      <label className="inline-block">
        <input
          type="file"
          accept={accept}
          className="hidden"
          onChange={(e) => {
            const file = e.target.files?.[0];
            if (file) handleFile(file);
          }}
        />
        <button type="button" className="btn btn-primary">
          {uploading ? "Mengunggah..." : "Unggah File"}
        </button>
      </label>
    </div>
  );
}
